export default ".pulse-loader {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n\r\n.pulse-loader div {\r\n  width: 12px;\r\n  height: 12px;\r\n  margin: 0 5px;\r\n  border-radius: 50%;\r\n  background-color: #3b44e2;\r\n  animation: pulse 1.4s infinite both;\r\n}\r\n\r\n.pulse-loader div:nth-child(2) {\r\n  animation-delay: 0.2s;\r\n}\r\n\r\n.pulse-loader div:nth-child(3) {\r\n  animation-delay: 0.4s;\r\n}\r\n\r\n@keyframes pulse {\r\n  0%,\r\n  100% {\r\n    opacity: 0.5;\r\n    transform: scale(0.75);\r\n  }\r\n  50% {\r\n    opacity: 1;\r\n    transform: scale(1);\r\n  }\r\n}\r\n";